                sampleCategoryList: [],
                speciesList: [],
                extractContentList: [],
                extractMethodList: [],
                checkMethodsList: [],
                libraryTypeList: [],
                libraryCheckMethodList: [],
                seqPlatformList: [],
                dataAmountUnitList: [],
                kitList: [],
                projectTypeDisabled: false,
                priorityDisabled: false,
                sampleCategoryDisabled: false,
                speciesDisabled: false,
                planCompleteTimeDisabled: false,
                specialRequireDisabled: false,
                pickerOptions0: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 8.64e7;
                    }
                },
                time: '',
            };
        },

        mounted () {
            var title = this.$route.query.title;
            if(title == undefined || title=="project"){
                var id = null;
                var secondParentId = null;
                for (let i = 0; i < this.typeArray.length; i++) {
                    this.getDict(id, secondParentId, this.typeArray[i].type);
                }
                this.form.id = this.$route.query.projectId;
                if (this.form.id != null) {
                    this.getProject();
                } else {
                    this.$store.commit("setProjectNo", null);
                }
            }
        },
        methods: {
            stay(){
                this.dialogVisible = false;
                this.edit=false;
                this.readOnly();
            },
            readOnly(){//整个页面只读状态
                var input = document.getElementsByTagName("input");
                for (var i = 0; i < input.length; i++) {
                    input[i].readOnly = true;
                }
                this.projectTypeDisabled = true;
                this.priorityDisabled = true;
                this.sampleCategoryDisabled = true;
                this.speciesDisabled = true;
                this.planCompleteTimeDisabled = true;
                this.specialRequireDisabled = true;
                this.jobsDisabled = true;
                var jobs = this.form.jobs;
                for (var i = 0; i < jobs.length; i++) {
                    this.$set(this.taskEnableList, i, !this.taskEnableList[i]);
                }
            },
            cancel(){
                this.edit = false;
                this.readOnly();
            },
            editForm(){
                var input = document.getElementsByTagName("input");
                for (var i = 0; i < input.length; i++) {
                    input[i].readOnly = false;
                }
                document.getElementsByName("projectNo")[0].readOnly=true;
                this.edit = true;
                this.projectTypeDisabled = false;
                this.priorityDisabled = false;
                this.sampleCategoryDisabled = false;
                this.speciesDisabled = false;
                this.planCompleteTimeDisabled = false;
                this.specialRequireDisabled = false;
                this.jobsDisabled = false;
                var jobs = this.form.jobs;
                for (var i = 0; i < jobs.length; i++) {
                    this.$set(this.taskEnableList, jobs[i].type-1, !this.taskEnableList[ jobs[i].type-1]);
                }
            },
            submitForm() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.form.jobs == null || this.form.jobs.length <= 0) {
                            this.$message({type: 'info', message: '请选择工作内容'});
                        } else {
                            this.readOnly();
                            if (this.form.planCompleteTime != null) {
                                this.form.planCompleteTime = Util.dateUtil.dateFormatNewDate(this.form.planCompleteTime);
                            }
                            if (this.form.id == null) {
                                Project.create(this.form).then((response) => {
                                    if (response.code == 0) {
                                        this.$store.commit("setProjectNo",response.data.no);
                                        this.$store.commit("setProjectId",response.data.id);
                                        this.$message({type: 'success', message: '创建项目成功'});
                                        this.form.id = response.data.id;
                                        this.form.no = response.data.no;
                                        this.dialogVisible = true;
                                    } else {
                                        this.$message({type: 'error', message: '创建项目失败'});
                                    }
                                });
                            } else {
                                Project.update(this.form).then((response) => {
                                    if (response.code == 0) {
                                        this.$store.commit("setProjectNo",this.form.no);
                                        this.$store.commit("setProjectId",this.form.id);
                                        this.$message({type: 'success', message: '更新项目成功'});
                                        this.dialogVisible = true;
                                    } else {
                                        this.$message({type: 'error', message: '更新项目失败'});
                                    }
                                });
                            }
                        }
                    } else {
                        this.$alert("信息填写错误")
                        return false;
                    }
                });
            },
            changeTaskStatus(index){
                if (!this.jobsDisabled) {
                    this.$set(this.taskEnableList, index, !this.taskEnableList[index]);
                    if (this.taskEnableList[index]) {
                        this.form.jobs.push(this.jobs[index]);
                    } else {
                        for (var i = 0; i < this.form.jobs.length; i++) {
                            if (this.form.jobs[i].type == this.jobs[index].type) {
                                this.form.jobs.splice(i, 1);
                            }
                        }
                    }
                }
                if (this.form.id == null) {
                    Object.assign(this.$data.jobs, this.$options.data())
                }
            },
            getProject(){
                if (this.form.id != null) {
                    Project.getById(this.form.id).then((response) => {
                        if (response.code == 0) {
                            this.readOnly();
                            this.form = response.data;
                            var jobs = this.form.jobs;
                            for (var i = 0; i < jobs.length; i++) {
                                var job = jobs[i];
                                var type = job.type;
                                this.jobs[type - 1].params = job.params;
                            }
                        }
                    });
                }
            },
            getDict(id, secondParentId, type){
                DataDict.get({
                    parentId: id,
                    secondParentId: secondParentId,
                    type: type
                }).then(response => {
                    if (response.code == 0) {
                        if (type == "4") {
                            this.sampleCategoryList = response.data;
                        } else if (type == "3") {
                            this.speciesList = response.data;
                        } else if (type == "1") {
                            this.projectTypeList = response.data;
                        } else if (type == "2") {
                            this.priorityList = response.data;
                        } else if (type == "7") {
                            this.extractContentList = response.data;
                        } else if (type == "9") {
                            this.checkMethodsList = response.data;
                        } else if (type == "10") {
                            this.libraryCheckMethodList = response.data;
                        } else if (type == "13") {
                            this.seqPlatformList = response.data;
                        } else if (type == "15") {
                            this.dataAmountUnitList = response.data;
                        } else if (id != null && secondParentId != null) {
                            this.extractMethodList = response.data;
                        } else if (type == "8") {
                            this.extractMethodList = response.data;
                        }
                    }
                });
            },
            //根据物种分类和样本提取内容查询提取方法
            extractContentChange(){
                let secondParentId = this.form.species;
                let id = this.jobs[0].params.extractContent;
                let type = "8";
                this.getDict(id, secondParentId, type);
            },
        }
    };

</script>

<style lang="scss" scoped>
    @import "~styles/variable";

    .task-line-wrapper {
        position: absolute;
        left: 0;
        top: 30px;
        bottom: 50px;
        margin-left: -10px;
        box-sizing: border-box;
        width: 16.66%;
        text-align: center;

        .task-line {
            display: inline-block;
            width: 2px;
            height: 100%;
            background-color: $color-text-ll;

        }
    }

    .task-item-wrapper {
        margin: 15px 0;

        &.disable {
            input, select {
                pointer-events: none;
            }
            .task-icon {
                fill: $color-theme-l !important;
            }
            .task-text {
                color: $color-theme-l !important;;
            }
        }

        .task-icon-wrapper {
            text-align: center;
            position: relative;
            z-index: 10;
            background-color: #fff;
            padding: 10px;

            .task-icon {
                width: 48px;
                height: 40px;
                fill: $color-theme;

            }

            .task-text {
                color: $color-theme;
                font-size: 14px;
            }
        }

        .task-item-content {
            background-color: $color-text-lll;
            border-radius: 10px;
            padding-top: 22px;
            position: relative;
            &:after {
                color: white;
                content: '';
                position: absolute;
                width: 0;
                height: 0;
                border: 8px solid;
                border-right-color: $color-text-lll;
                top: 30px;
                left: -15px;
                z-index: 100;
            }
        }
    }

</style>


